import React from "react";
import Image from "next/image";

export default function RPTLogoTitle({ small = false }) {
  // Define exact dimensions for better CLS
  const size = small ? 32 : 14;
  
  return (
    <div className={`flex flex-row items-center ${small ? "" : "mb-6"}`}>
      <Image
        src="/RPTracker/RPTLogo.png"
        alt="RPTracker Logo"
        width={size}
        height={size}
        priority={true}
        className={`${small ? "mr-2" : "mr-4"} object-contain drop-shadow-md`}
      />
      <div
        className={`font-bold bg-gradient-to-r from-green-700 to-green-900 bg-clip-text text-transparent ${
          small ? "text-xl" : "text-5xl"
        }`}
      >
        RPTracker
      </div>
    </div>
  );
}
